import {
  AppBar,
  Toolbar,
  Typography,
  makeStyles,
  IconButton,
  Drawer,
  List,
  Divider,
  ListItem,
  ListItemIcon,
  TextField,
  Accordion,
  AccordionDetails,
  AccordionSummary,
  ListItemText,
  Badge,
} from "@material-ui/core";
import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { logout } from "../actions/auth";
import {
  ShoppingCart,
  Brightness7,
  Brightness4,
  AccountCircle,
  Menu,
  MoveToInbox,
  ExpandMore,
  Mail,
  SearchSharp,
} from "@material-ui/icons";

const useStyles = makeStyles((theme) => ({
  title: {
    marginLeft: 20,
    textDecoration: "none",
    flexGrow: 1,
  },
  rightIcons: { flexGrow: 1 },
  navLink: {
    textDecoration: "none",
    color: "inherit",
    // margin: 5,
  },
  list: {
    width: 250,
  },
  fullList: {
    width: "auto",
  },
}));

const Appbar = ({ isAuthenticated, logout, checked, onChange }) => {
  const classes = useStyles();
  const authLinks = (
    <Link className={classes.navLink} onClick={logout}>
      <Typography variant="button">Logout</Typography>
    </Link>
  );

  // const guestLinks = (
  //   <>
  //     <Link className={classes.navLink} exact to="/login">
  //       <Typography variant="button">Login</Typography>
  //     </Link>
  //
  //     <Link className={classes.navLink} exact to="/signup">
  //       <Typography variant="button">Sign Up</Typography>
  //     </Link>
  //   </>
  // );

  const [drawerstate, setDrawerState] = React.useState({ open: false });

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setDrawerState({ ...drawerstate, [anchor]: open });
  };
  const [expanded, setExpanded] = React.useState(false);

  const handleExpand = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };
  const list = (anchor) => (
    <div
      className={classes.list}
      // onClick={toggleDrawer(anchor, false)}
      // onKeyDown={toggleDrawer(anchor, false)}
    >
      <Toolbar>
        <Link
          className={classes.title}
          to="/"
          onClick={toggleDrawer(anchor, false)}
        >
          <Typography variant="h6" color="textPrimary">
            SAKAR
          </Typography>
        </Link>
      </Toolbar>
      <Accordion
        expanded={expanded === "panel1"}
        onChange={handleExpand("panel1")}
      >
        <AccordionSummary
          expandIcon={<ExpandMore />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography>General settings</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <List>
            {["Inbox", "Starred", "Send email", "Drafts"].map((text, index) => (
              <ListItem button key={text} onClick={toggleDrawer(anchor, false)}>
                <ListItemIcon>
                  {index % 2 === 0 ? <MoveToInbox /> : <Mail />}
                </ListItemIcon>
                <ListItemText primary={text} style={{ textAlign: "right" }} />
              </ListItem>
            ))}
          </List>
        </AccordionDetails>
      </Accordion>

      {/* <Divider /> */}
      <List>
        {["All mail", "Trash", "Spam"].map((text, index) => (
          <ListItem button key={text} onClick={toggleDrawer(anchor, false)}>
            <ListItemIcon>
              {index % 2 === 0 ? <MoveToInbox /> : <Mail />}
            </ListItemIcon>
            <ListItemText primary={text} style={{ textAlign: "right" }} />
          </ListItem>
        ))}
      </List>
    </div>
  );
  return (
    <>
      <AppBar position="sticky" color="inherit">
        <Toolbar>
          <div className={classes.rightIcons}>
            <IconButton color="inherit" onClick={toggleDrawer("open", true)}>
              <Menu />
            </IconButton>

             {isAuthenticated ? authLinks : ""}
          </div>

          <Link className={classes.title} to="/">
            <Typography variant="h5" color="primary">
              SAKAR
            </Typography>
          </Link>
          <IconButton color="inherit" onClick={onChange}>
            {checked ? <Brightness7 /> : <Brightness4 />}
          </IconButton>
        </Toolbar>
      </AppBar>
      <Toolbar>
        <div className={classes.rightIcons}>
          <Link className={classes.navLink} exact to="/profile">
            <IconButton color="inherit" size="medium">
              <AccountCircle />
            </IconButton>
          </Link>
          <IconButton color="inherit">
            <Badge
              anchorOrigin={{
                vertical: "top",
                horizontal: "left",
              }}
              badgeContent={4}
              color="secondary"
            >
              <ShoppingCart />
            </Badge>
          </IconButton>
        </div>
        <form noValidate autoComplete="off">
          <TextField
            style={{ marginTop: 5, width: "14rem" }}
            id="search"
            label="جستجو"
            variant="outlined"
            size="small"
            InputProps={{
              endAdornment: (
                <IconButton color="inherit" size="small">
                  <SearchSharp />
                </IconButton>
              ),
            }}
          />
        </form>
      </Toolbar>
      <Divider />

      <Drawer
        // BackdropProps={{ invisible: true }}
        anchor={"left"}
        open={drawerstate["open"]}
        onClose={toggleDrawer("open", false)}
      >
        {list("open")}
      </Drawer>
    </>
  );
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, { logout })(Appbar);
