import axios from "axios";
import {
  LOAD_ITEMS_SUCCESS,
  LOAD_ITEMS_FAIL,
  LOAD_ITEM_SUCCESS,
  LOAD_ITEM_FAIL,
  LOAD_USER_DETAIL_SUCCESS,
  LOAD_USER_DETAIL_FAIL,
  LOAD_ADDRESS_SUCCESS,
  LOAD_ADDRESS_FAIL,
} from "../actions/types";

export const load_items = () => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "application/json",
      "Accept": "application/json",
    },
  };

  try {
    const res = await axios.get(
      `${process.env.REACT_APP_API_URL}/api/items-list/`,
      config
    );

    dispatch({
      type: LOAD_ITEMS_SUCCESS,
      payload: res.data,
    });
  } catch (err) {
    dispatch({
      type: LOAD_ITEMS_FAIL,
    });
  }
};

export const load_item = (itemId) => async (dispatch) => {
  const csrftoken = getCookie('csrftoken')
  const config = {
    headers: {
      "Content-Type": "application/json",
      "Accept": "application/json",
      'X-CSRFToken':csrftoken,
    },
  };
  try {
    const res = await axios.get(
      `${process.env.REACT_APP_API_URL}/api/item-detail/${itemId}/`,
      config
    );

    dispatch({
      type: LOAD_ITEM_SUCCESS,
      payload: res.data,
    });
  } catch (err) {
    dispatch({
      type: LOAD_ITEM_FAIL,
    });
  }
};


export const load_user_detail = (userId) => async dispatch => {
    if (localStorage.getItem('access')) {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `JWT ${localStorage.getItem('access')}`,
                'Accept': 'application/json'
            }
        };

        try {
            const res = await axios.get(`${process.env.REACT_APP_API_URL}/api/user-detail/${userId}/`, config);

            dispatch({
                type: LOAD_USER_DETAIL_SUCCESS,
                payload: res.data
            });
        } catch (err) {
            dispatch({
                type: LOAD_USER_DETAIL_FAIL
            });
        }
    } else {
        dispatch({
            type: LOAD_USER_DETAIL_FAIL
        });
    }
};


export const load_address = (userId) => async dispatch => {
    if (localStorage.getItem('access')) {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `JWT ${localStorage.getItem('access')}`,
                'Accept': 'application/json'
            }
        };

        try {
            const res = await axios.get(`${process.env.REACT_APP_API_URL}/api/address-list/${userId}/`, config);

            dispatch({
                type: LOAD_ADDRESS_SUCCESS,
                payload: res.data
            });
        } catch (err) {
            dispatch({
                type: LOAD_ADDRESS_FAIL
            });
        }
    } else {
        dispatch({
            type: LOAD_ADDRESS_FAIL
        });
    }
};

function getCookie(name) {
    var cookieValue = null;
    if (document.cookie && document.cookie !== '') {
        var cookies = document.cookie.split(';');
        for (var i = 0; i < cookies.length; i++) {
            var cookie = cookies[i].trim();
            // Does this cookie string begin with the name we want?
            if (cookie.substring(0, name.length + 1) === (name + '=')) {
                cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                break;
            }
        }
    }
    return cookieValue;
}